<template>
  <app-header />
  <h1>Update Restaurant page</h1>
  <div class="add">
    <input type="text" placeholder="Enter Name" name="name" v-model="restaurant.name" />
    <input
      type="text"
      placeholder="Enter address"
      name="name"
      v-model="restaurant.address"
    />
    <input
      type="text"
      placeholder="Enter contact"
      name="name"
      v-model="restaurant.phone"
    />
    <button @click="updateRestaurant">Update Restaurant</button>
  </div>
</template>
<script>
import header from "./Header.vue";
import axios from "axios"
export default {
  name: "update-restaurant",
  components: {
    "app-header": header,
  },
  data() {
    return {
      restaurant: {
        name: "",
        address: "",
        phone: "",
      },
    };
  },
  async mounted() {
    let user = localStorage.getItem("user-info");
    if (!user) {
      this.$router.push({ name: "SignUp" });
    }
    let result = await axios.get("http://localhost:3000/restaurants/"+this.$route.params.id);
    console.log(result.data);
    this.restaurant.name = result.data.name;
    this.restaurant.address = result.data.address;
    this.restaurant.phone = result.data.phone;
  },
  methods: {
            async updateRestaurant(){
                let add = await axios.put("http://localhost:3000/restaurants/"+this.$route.params.id,{
                    name:this.restaurant.name,
                    address:this.restaurant.address,
                    phone:this.restaurant.phone,
                });
                if(add.status===200){
                    this.$router.push({name:"home-page"});
                }
                }
        }
};
</script>
