<template>
<app-header />
    <h1>Hello Users welcome to Add restaurant page </h1>
    <div class="add">
    <input type="text" placeholder="Enter Name" name="name" v-model="restaurant.name" />
    <input type="text" placeholder="Enter address" name="name" v-model="restaurant.address" />
    <input type="text" placeholder="Enter contact" name="name" v-model="restaurant.phone" />
    <button @click="addRestaurant">Add Restaurant</button>
    </div>
</template>
<script>
import header from './Header.vue'
import axios from 'axios'
    export default {
        name: 'home-page',
        components: {
            'app-header': header,
        },
        data (){
                return {
                    restaurant:{
                        name:'',
                        address:'',
                        phone:''
                    },
                }
            },
        mounted()
        {  
            let user = localStorage.getItem('user-info');
            if(!user) {
                this.$router .push({name:'SignUp'})
            }
        },
        methods: {
            async addRestaurant(){
                let add = await axios.post('http://localhost:3000/restaurants',{
                    name:this.restaurant.name,
                    address:this.restaurant.address,
                    phone:this.restaurant.phone,
                });
                if(add.status===201){
                    this.$router.push({name:"home-page"});
                }
                }
        }
    }
</script>