<script>
import header from "./Header.vue";
import axios from "axios";
export default {
  name: "home-page",
  components: {
    "app-header": header,
  },
  data() {
    return {
      name: "",
      restaurantList: [],
      menu: [],
    };
  },
  async mounted() {
    let user = localStorage.getItem("user-info");
    this.name = JSON.parse(user).name;
    let restaurantName = await axios.get("http://localhost:3000/restaurants");
    this.restaurantList = restaurantName.data;
    console.log(this.restaurantList);
    if (!user) {
      this.$router.push({ name: "SignUp" });
    }
    let menu = await axios.get("http://localhost:3000/menu");
    this.menu = menu.data;
  },
  methods: {
    hero() {
      this.$router.push({"name":"creator"});
    }
  }
};
</script>
